}

.cds--cc--heatmap g.highlighter-hidden {
  visibility: hidden;
}
.cds--cc--heatmap g.cell-highlight line {
  stroke: white;
  stroke-width: 1px;
}
.cds--cc--heatmap g.cell-2 line {
  stroke: white;
  stroke-width: 2px !important;
}
.cds--cc--heatmap g.multi-cell line {
