}
@keyframes shimmer {
  0% {
    stroke: var(--cds-layer-accent-01, #e0e0e0);
  }
  20% {
    stroke: #ffffff;
    opacity: 0.5;
  }
  100% {
    stroke: var(--cds-layer-accent-01, #e0e0e0);
  }
}

